"use client"

import { useState, useEffect } from "react"
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Button } from "@/components/ui/button"
import { Loader2, Calendar, Clock, Info } from "lucide-react"

export default function ProductFormModal({ isOpen, onClose, initialData, onSuccess }: any) {
  const [isLoading, setIsLoading] = useState(false)
  const [formData, setFormData] = useState({
    productName: "",
    description: "",
    category: "",
    durationMin: 60,
    restTime: 15,
    workingHours: 3,
    startTime: 1400,
    endTime: 1700,
    price: 120000,
    dayOfWeek: "THURSDAY"
  })

  useEffect(() => {
    if (initialData) {
      setFormData(initialData)
    } else {
      setFormData({
        productName: "",
        description: "",
        category: "",
        durationMin: 60,
        restTime: 15,
        workingHours: 3,
        startTime: 1400,
        endTime: 1700,
        price: 120000,
        dayOfWeek: "THURSDAY"
      })
    }
  }, [initialData, isOpen])

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsLoading(true)

    const token = localStorage.getItem("accessToken")
    const method = initialData ? "PUT" : "POST"
    const url = initialData 
      ? `http://34.50.7.8:3000/v1/products/${initialData.productId}`
      : "http://34.50.7.8:3000/v1/products"

    try {
      const payload = {
        ...formData,
        durationMin: Number(formData.durationMin),
        restTime: Number(formData.restTime),
        workingHours: Number(formData.workingHours),
        startTime: Number(formData.startTime),
        endTime: Number(formData.endTime),
        price: Number(formData.price)
      }

      const res = await fetch(url, {
        method,
        headers: { 
          "Authorization": `Bearer ${token}`,
          "Content-Type": "application/json",
          "Accept": "application/json"
        },
        body: JSON.stringify(payload)
      })

      if (res.ok) {
        onSuccess()
        onClose()
      } else {
        const result = await res.json()
        alert(`실패: ${result.message || "오류가 발생했습니다."}`)
      }
    } catch (error) {
      alert("서버와 통신 중 에러가 발생했습니다.")
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-xl max-h-[90vh] overflow-y-auto rounded-[1.5rem] border-none shadow-2xl p-0 bg-white">
        <DialogHeader className="p-8 pb-0">
          <DialogTitle className="text-2xl font-bold tracking-tight text-slate-900">
            {initialData ? "상품 정보 수정" : "새로운 상담 상품 등록"}
          </DialogTitle>
          <p className="text-sm text-slate-500 font-medium mt-1">유저들에게 제공할 상담의 상세 내용을 설정하세요.</p>
        </DialogHeader>
        
        <form onSubmit={handleSubmit} className="p-8 space-y-6">
          <div className="space-y-5">
            {/* 기본 정보 */}
            <div className="space-y-4">
              <div className="space-y-2">
                <Label className="text-xs font-bold text-slate-500 uppercase tracking-wider ml-1">상담 상품명</Label>
                <Input 
                  value={formData.productName} 
                  onChange={e => setFormData({...formData, productName: e.target.value})} 
                  placeholder="예: 1:1 커리어 멘토링 60분" 
                  className="h-11 rounded-xl border-slate-200 bg-slate-50/30 font-semibold focus:bg-white transition-all shadow-sm" 
                  required 
                />
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label className="text-xs font-bold text-slate-500 uppercase tracking-wider ml-1">카테고리</Label>
                  <Input 
                    value={formData.category} 
                    onChange={e => setFormData({...formData, category: e.target.value})} 
                    placeholder="커리어, 재무 등" 
                    className="h-11 rounded-xl border-slate-200 bg-slate-50/30 font-semibold shadow-sm" 
                    required 
                  />
                </div>
                <div className="space-y-2">
                  <Label className="text-xs font-bold text-slate-500 uppercase tracking-wider ml-1">가격 (KRW)</Label>
                  <Input 
                    type="number" 
                    value={formData.price} 
                    onChange={e => setFormData({...formData, price: Number(e.target.value)})} 
                    className="h-11 rounded-xl border-slate-200 bg-slate-50/30 font-semibold shadow-sm" 
                    required 
                  />
                </div>
              </div>

              <div className="space-y-2">
                <Label className="text-xs font-bold text-slate-500 uppercase tracking-wider ml-1">상담 상세 설명</Label>
                <Textarea 
                  value={formData.description} 
                  onChange={e => setFormData({...formData, description: e.target.value})} 
                  placeholder="상담 과정과 제공되는 가치에 대해 설명해주세요." 
                  className="min-h-[100px] rounded-xl border-slate-200 bg-slate-50/30 font-medium py-3 shadow-sm resize-none" 
                  required 
                />
              </div>
            </div>

            {/* 스케줄 설정 세션 - 대시보드 카드 스타일 적용 */}
            <div className="p-6 bg-slate-50 rounded-2xl border border-slate-100 space-y-5">
              <div className="flex items-center gap-2 text-primary font-bold text-sm">
                <Clock className="h-4 w-4" />
                <span>상담 스케줄링 규칙</span>
              </div>
              
              <div className="grid grid-cols-3 gap-4">
                <div className="space-y-2">
                  <Label className="text-[10px] font-bold text-slate-400 uppercase">상담 요일</Label>
                  <select 
                    value={formData.dayOfWeek} 
                    onChange={e => setFormData({...formData, dayOfWeek: e.target.value})} 
                    className="w-full h-10 rounded-lg border-slate-200 bg-white text-xs font-bold px-2 focus:ring-2 ring-primary/20 outline-none shadow-sm"
                  >
                    {["MONDAY", "TUESDAY", "WEDNESDAY", "THURSDAY", "FRIDAY", "SATURDAY", "SUNDAY"].map(day => (
                      <option key={day} value={day}>{day}</option>
                    ))}
                  </select>
                </div>
                <div className="space-y-2">
                  <Label className="text-[10px] font-bold text-slate-400 uppercase">상담 시간(분)</Label>
                  <Input type="number" value={formData.durationMin} onChange={e => setFormData({...formData, durationMin: Number(e.target.value)})} className="h-10 rounded-lg border-slate-200 bg-white font-bold text-xs shadow-sm" />
                </div>
                <div className="space-y-2">
                  <Label className="text-[10px] font-bold text-slate-400 uppercase">휴식 시간</Label>
                  <Input type="number" value={formData.restTime} onChange={e => setFormData({...formData, restTime: Number(e.target.value)})} className="h-10 rounded-lg border-slate-200 bg-white font-bold text-xs shadow-sm" />
                </div>
              </div>

              <div className="grid grid-cols-3 gap-4 pt-1">
                <div className="space-y-2">
                  <Label className="text-[10px] font-bold text-slate-400 uppercase">시작 (HHMM)</Label>
                  <Input type="number" value={formData.startTime} onChange={e => setFormData({...formData, startTime: Number(e.target.value)})} className="h-10 rounded-lg border-slate-200 bg-white font-bold text-xs shadow-sm" />
                </div>
                <div className="space-y-2">
                  <Label className="text-[10px] font-bold text-slate-400 uppercase">종료 (HHMM)</Label>
                  <Input type="number" value={formData.endTime} onChange={e => setFormData({...formData, endTime: Number(e.target.value)})} className="h-10 rounded-lg border-slate-200 bg-white font-bold text-xs shadow-sm" />
                </div>
                <div className="space-y-2">
                  <Label className="text-[10px] font-bold text-slate-400 uppercase">하루 상담 총량</Label>
                  <Input type="number" value={formData.workingHours} onChange={e => setFormData({...formData, workingHours: Number(e.target.value)})} className="h-10 rounded-lg border-slate-200 bg-white font-bold text-xs shadow-sm" />
                </div>
              </div>
            </div>
          </div>

          <div className="flex gap-3 pt-2">
            <Button 
              type="button" 
              variant="outline" 
              onClick={onClose}
              className="flex-1 h-12 rounded-xl font-bold border-slate-200 text-slate-600 hover:bg-slate-50 transition-all"
            >
              취소
            </Button>
            <Button 
              type="submit" 
              disabled={isLoading} 
              className="flex-[2] h-12 rounded-xl font-bold shadow-lg shadow-primary/20 bg-primary hover:bg-primary/90 transition-all text-white"
            >
              {isLoading ? <Loader2 className="animate-spin mr-2 h-4 w-4" /> : null}
              {initialData ? "정보 업데이트" : "상품 등록하기"}
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  )
}